body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.headerBg {
  
  padding: 45px;
}

.header2Bg {
  padding: 15px;
}

.headerBg h1 {
  text-align: center;
}

.height-adjust-30 {
  padding-bottom: 30px;
}
.height-adjust-180 {
  height: 40vh;
}

.height-adjust-375 {
  padding-bottom: 375px;
}
.height-adjust-60 {
  padding-bottom: 60px;
}

.bold-text {
  font-weight: bold;
}

.italic-text {
  font-style: italic;
}



.footerDiv ul {
  list-style: none;
}

.footerDiv p {
  margin-bottom: 0;
  
}

.footerDiv h5 {
  font-size: 16px;
}
.footerDiv a {
  text-decoration: none;
  font-size: 12px;
}

.footerDiv a:hover {
  text-decoration: underline;
}
